import React, { Component } from 'react';
export class Projects extends Component {

     render() {
        let {pname,pdesp,time} = this.props;
        return (
                <>
         

                <div className="lg:w-1/4 md:w-1/2 p-4">
                <a className="block relative h-48 rounded overflow-hidden">
                <img alt="ecommerce" className="object-cover object-center w-full h-full block" src="https://dummyimage.com/420x260"/>
                </a>
                <div className="mt-4">
                <h2 className="text-gray-900 title-font text-lg font-medium"><a href="" >{pname}</a></h2>
                <h3 className="text-gray-500 text-xls tracking-widest title-font mb-1">{pdesp}</h3>
                <p className="mt-1">{time}</p>
                </div>
                </div>
       

   
                </>
        )
    }
}

export default Projects


