import React, { Component } from 'react';
import Projects from './Projects';
export class Products extends Component {
  proj = [
    {
        projectname : "InterviewByte",
        projdescription : "Online Mock Interview Platform",
        duration : "July 2021 - October 2021"
    },
    {
      projectname : "Jeevandaan",
      projdescription : "Platform for Organ Donation and Reception",
      duration : "February 2021 - May 2021"
    },
    {
      projectname : "Mrityunjay",
      projdescription : "Platform for Organ Donation and Reception",
      duration : "February 2021 - May 2021"
    },
    {
      projectname : "Exploratory Data Analysis On Sports Dataset",
      projdescription : "Platform for Organ Donation and Reception",
      duration : "February 2021 - May 2021"
    },
    {
      projectname : " Student's Percentage Prediction Using Linear Regression",
      projdescription : "Platform for Organ Donation and Reception",
      duration : "February 2021 - May 2021"
    },
    {
      projectname : "Black Hole Animation - Blender 3D",
      projdescription : "Platform for Organ Donation and Reception",
      duration : "February 2021 - May 2021"
    },
    {
      projectname : "Jelly Animation - Blender 3D",
      projdescription : "Platform for Organ Donation and Reception",
      duration : "February 2021 - May 2021"
    },
    {
      projectname : "Koenigsegg Agera R Animation - Blender 3D & RigaCar",
      projdescription : "Platform for Organ Donation and Reception",
      duration : "February 2021 - May 2021"
    }
]

constructor(){
    super();
    this.state = {

        proj : this.proj 

    }
}
  render() {
    return (
     
          <>
          <section className="text-gray-600 body-font" >
          <div className="container px-5 py-24 mx-auto">
          <div className="flex flex-wrap -m-4">
          {this.state.proj.map((element)=>{

            return <Projects  key={element.projectname} pname={element.projectname} pdesp={element.projdescription} time={element.duration}/>
                   

          })}
            </div>
            </div>
            </section>
          </>

      
    )
  }
}

export default Products

